/* Small menu. */
.toggle-nav {
  display: inline-block;
  position: absolute;
  top: 5%;
  right: 15%;
  z-index: 99999;
  border: 0;
  box-shadow: none;
  background: transparent;
  outline: transparent;
  transition: transform 0.5s; }

.toggle__bar {
  display: block;
  width: 30px;
  height: 2px;
  background-color: #cccccc;
  border-radius: 1px;
  margin: 8px auto; }

/* toggled state of menu. */
.menu-toggle--fixed {
  position: fixed;
  top: 4%;
  height: 60px;
  width: 50px; }
  .menu-toggle--fixed .toggle__bar {
    margin: 0;
    display: block;
    position: absolute;
    transition: opacity 0.4s , transform 0.4s; }
    .menu-toggle--fixed .toggle__bar:nth-child(2) {
      transform: rotate(-135deg); }
    .menu-toggle--fixed .toggle__bar:nth-child(3) {
      transform: rotate(135deg); }
    .menu-toggle--fixed .toggle__bar:last-child {
      transform: translateX(-300%);
      opacity: 0; }

.main-nav.toggled {
  opacity: 1;
  z-index: 99998; }
  .main-nav.toggled body {
    right: 50%;
    width: 100%; }
  .main-nav.toggled .toggle-nav {
    z-index: 99999; }

.main-nav.toggled ul {
  transform: translateX(0);
  opacity: 1;
